import { Component, OnInit } from '@angular/core';
import { Http, Response, URLSearchParams } from "@angular/http";
import { OrderByPipe } from "../orderBy";
// import 'rxjs/add/operator/map';

const BASE_HOST = "https://teddyscript.herokuapp.com";

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['../app.component.css']
})
export class HomeComponent {
  msg = "";
  list: Array<any> = [];

  constructor(private http: Http) {

  }

  ngOnInit() {
    $(document).ready(function(){
      $('#alert-error').hide();
    })

    $('#alert-error .close').click(function(e) {
        e.preventDefault();
        $(this).closest('#alert-error').slideUp();
    });
  }

  getLinkYoutube(link: any) {
    $("#alert-error").slideUp();
    this.list = [];
    $("#_frame").remove();

    if (link !== "") {
      let params: URLSearchParams = new URLSearchParams();
      params.set("url", link);

      this.http.get(BASE_HOST + "/get_link", { search: params })
      .subscribe((res:Response) => {
        var result = res.json();

        if (result["result"] === null) {
          this.msg = result["msg"];
          $("#alert-error").slideDown();

          setTimeout(function() {
            $("#alert-error").slideUp();
          }, 3000);
        }else {
          var oj = result["result"];
          var keys = Object.keys(result["result"]);

          var videoid = link.match(/(?:https?:\/{2})?(?:w{3}\.)?youtu(?:be)?\.(?:com|be)(?:\/watch\?v=|\/)([^\s&]+)/);
          if(videoid != null) {
            $("#btnGetLink").after('<div id="_frame"><iframe width="560" height="315" src="https://www.youtube.com/embed/' + videoid[1] + '" frameborder="0" allowfullscreen></iframe></div>')
          }

          keys.forEach((item) => {
            oj[item].name = item;
            this.list.push(oj[item]);
          });
        }
      })
    }else {
      this.msg = "Please enter the Youtube link."

      $("#alert-error").slideDown();

      setTimeout(function() {
        $("#alert-error").slideUp();
      }, 3000);
    }
  }

}



